@font-face {
  font-family: 'TextBook';
  src: url('TextBookBold.woff') format('woff');
  font-weight: bold;
  font-display: swap;
}

@font-face {
  font-family: 'TextBook';
  src: url('TextbookNormal.woff') format('woff');
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Arimo';
  src: url('Arimo-Regular.ttf');
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Arimo';
  src: url('Arimo-Bold.ttf');
  font-weight: bold;
  font-display: swap;
}

@font-face {
  font-family: 'FontAwesome';
  src: url('fontawesome-webfont.woff');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}



